html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #DEE8EC;
  font-family: "Inconsolata", sans-serif, arial;
  font-size: 1.6rem;
}

a {
  cursor: pointer;
  text-decoration: none;
}

p {
  margin: 0;
}

img,
picture {
  display: block;
  max-width: 100%;
  width: 100%;
}

section {
  margin: 5rem 0;
}

input,
textarea {
  border: none;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.push-left {
  margin-left: auto;
}

/** Media Queries **/
h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  color: #023047;
  text-align: center;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #023047;
  font-size: 2rem;
}

.badge__orange {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 0.5rem;
  background-color: #F3FCFF;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  padding: 0.5rem 1rem;
  width: 18rem;
  letter-spacing: 0.1rem;
  border-left: 5px solid #FFB703;
}
.badge__orange .badge-icon {
  flex: 0.5rem;
}
.badge__orange .badge-icon image,
.badge__orange .badge-icon picture {
  aspect-ratio: 3/2;
  object-fit: contain;
}
.badge__orange .badge-text {
  flex: 3;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.badge__orange p {
  font-size: 1.8rem;
  color: #023047;
}

.badge__blue {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 0.5rem;
  background-color: #F3FCFF;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  padding: 0.5rem 1rem;
  width: 18rem;
  letter-spacing: 0.1rem;
  border-left: 5px solid #219EBC;
}
.badge__blue .badge-icon {
  flex: 0.5rem;
}
.badge__blue .badge-icon image,
.badge__blue .badge-icon picture {
  aspect-ratio: 3/2;
  object-fit: contain;
}
.badge__blue .badge-text {
  flex: 3;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.badge__blue p {
  font-size: 1.8rem;
  color: #023047;
}

.badge__dark {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 0.5rem;
  background-color: #F3FCFF;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  padding: 0.5rem 1rem;
  width: 18rem;
  letter-spacing: 0.1rem;
  border-left: 5px solid #023047;
}
.badge__dark .badge-icon {
  flex: 0.5rem;
}
.badge__dark .badge-icon image,
.badge__dark .badge-icon picture {
  aspect-ratio: 3/2;
  object-fit: contain;
}
.badge__dark .badge-text {
  flex: 3;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.badge__dark p {
  font-size: 1.8rem;
  color: #023047;
}

.badge--inactive {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 0.5rem;
  background-color: #DEE8EC;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  padding: 0.5rem 1rem;
  width: 18rem;
  letter-spacing: 0.1rem;
}
.badge--inactive .badge-icon {
  flex: 0.5rem;
}
.badge--inactive .badge-icon image,
.badge--inactive .badge-icon picture {
  aspect-ratio: 3/2;
  object-fit: contain;
}
.badge--inactive .badge-text {
  flex: 3;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
.badge--inactive p {
  font-size: 1.8rem;
  color: #023047;
}
.badge--inactive .badge-icon {
  filter: grayscale(1);
}

.btn-primary {
  background-color: #219EBC;
  border-radius: 0.5rem;
  color: #F3FCFF;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1.5rem 3rem;
  transition: all 0.3s ease-in-out;
}
.btn-primary:hover {
  background-color: #197a91;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  margin-top: 7.5rem;
}
@media (min-width: 1024px) {
  .card-container {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  background-color: #F3FCFF;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  -webkit-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  -moz-box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  box-shadow: 1px 2px 9px -2px rgba(2, 48, 71, 0.42);
  position: relative;
  width: 100%;
}
@media (min-width: 768px) {
  .card {
    width: 30rem;
  }
}

.card-image {
  flex: 30rem;
  overflow: hidden;
  border-radius: 0.5rem 0.5rem 0 0;
}

.card-footer {
  display: flex;
  align-items: center;
  gap: 2rem;
  padding: 2rem 1rem;
}
.card-footer .card-link {
  width: 3rem;
  transition: filter 0.3s ease-in-out;
}
.card-footer .card-link:hover {
  filter: grayscale(1);
}
.card-footer h3 {
  margin: 0;
  font-size: 1.6rem;
}

.header {
  background-color: #DEE8EC;
  height: 100vh;
  max-width: 200rem;
  margin: 0 auto;
}

.top-menu {
  display: flex;
  padding: 2.5rem;
  justify-content: space-between;
}

.menu {
  display: none;
}
@media (min-width: 1024px) {
  .menu {
    display: block;
  }
}
.menu a {
  color: #023047;
  padding: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  transition: color 0.3s ease-in-out;
}
.menu a:hover {
  color: #FFB703;
  border-bottom: 1px solid #FFB703;
}

.container-title {
  margin-top: 2.5rem;
  padding: 2rem;
}
.container-title h1 {
  color: #023047;
  letter-spacing: 0.5rem;
  font-size: 3rem;
  font-weight: 900;
  text-align: left;
}
@media (min-width: 768px) {
  .container-title h1 {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .container-title h1 {
    font-size: 5rem;
  }
}

.text-shadow {
  color: #DEE8EC;
  display: block;
  font-size: 6rem;
  padding-top: 1rem;
  letter-spacing: 1rem;
  text-shadow: 0.2rem 0.2rem 1.2rem #023047;
}
@media (min-width: 768px) {
  .text-shadow {
    font-size: 13rem;
  }
}
@media (min-width: 1024px) {
  .text-shadow {
    font-size: 20rem;
    margin: 0;
  }
}

.container-social-networks {
  display: flex;
  gap: 5rem;
  margin-top: 3rem;
}
.container-social-networks a {
  width: 3rem;
  filter: drop-shadow(0.1rem 0.1rem 0.2rem #023047);
  transition: filter 0.2s ease-in-out;
}
@media (min-width: 768px) {
  .container-social-networks a {
    width: 5rem;
  }
}
.container-social-networks a:hover {
  filter: drop-shadow(0.1rem 0.1rem 0.2rem #8ECAE6);
}

.about-me {
  display: flex;
  flex-direction: column;
}

.container-resume {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.container-resume p {
  color: #023047;
  line-height: 1.5;
  letter-spacing: 0.1rem;
}
@media (min-width: 1024px) {
  .container-resume p {
    width: 50%;
  }
}
.container-resume .about-me {
  text-align: right;
}
@media (min-width: 1024px) {
  .container-resume .about-me {
    text-align: left;
  }
}
.container-resume .link-download {
  border-bottom: 2px solid #219EBC;
  color: #219EBC;
  font-size: 2rem;
  font-weight: 900;
  margin-right: 3rem;
  padding-bottom: 0.5rem;
  transition: all 0.2s ease-in-out;
}
.container-resume .link-download:hover {
  color: #FFB703;
  border-bottom: 2px solid #FFB703;
}

.profile-name {
  font-size: 3rem;
  font-weight: 700;
  text-align: right;
  margin: 0;
}
@media (min-width: 1024px) {
  .profile-name {
    font-size: 5rem;
  }
}
.profile-name .subtitle {
  display: block;
  font-size: 2.5rem;
  font-weight: 400;
}

.skills section {
  padding-bottom: 5rem;
}

.container-badge {
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  justify-content: space-evenly;
  margin-top: 5rem;
}

.front-end {
  border-width: 3px;
  border-style: solid;
  border-image: linear-gradient(to right, #FFB703, #DEE8EC) 1;
}
.front-end .skill-title {
  background-color: #FFB703;
  border-radius: 0px 0px 0.5rem 0px;
  color: #F3FCFF;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  text-align: center;
  width: 15rem;
}

.back-end {
  border-width: 3px;
  border-style: solid;
  border-image: linear-gradient(to right, #219EBC, #DEE8EC) 1;
}
.back-end .skill-title {
  background-color: #219EBC;
  border-radius: 0px 0px 0.5rem 0px;
  color: #F3FCFF;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  text-align: center;
  width: 15rem;
}

.others-skills {
  border-width: 3px;
  border-style: solid;
  border-image: linear-gradient(to right, #023047, #DEE8EC) 1;
}
.others-skills .skill-title {
  background-color: #023047;
  border-radius: 0px 0px 0.5rem 0px;
  color: #F3FCFF;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  text-align: center;
  width: 15rem;
}

.contact {
  background-color: #F3FCFF;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50rem;
  justify-content: space-between;
  padding-top: 2.5rem;
}

.container-mail .contact-text {
  font-size: 1.4rem;
  font-weight: 1.6rem;
  margin-bottom: 1.6666666667rem;
}
@media (min-width: 768px) {
  .container-mail .contact-text {
    font-size: 2.2rem;
  }
}

.mail-contact {
  display: block;
  text-align: center;
  color: #8ECAE6;
  font-weight: 900;
  font-size: 2.2rem;
  letter-spacing: 0.3rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  .mail-contact {
    font-size: 5rem;
    letter-spacing: 0.6rem;
  }
}
.mail-contact:hover {
  color: #219EBC;
  border-bottom: 3px solid #219EBC;
}

.contact-social-networks {
  display: flex;
  gap: 5rem;
  justify-content: center;
  margin-bottom: 3rem;
}
.contact-social-networks a {
  max-width: 4rem;
}

.footer {
  margin-top: 10rem;
}
.footer p {
  color: #023047;
  font-size: 1.8rem;
  text-align: center;
}

.container-projects {
  display: flex;
  gap: 5rem;
}